
import React from 'react';
import './navbar.css';
import { Nav,NavDropdown,Navbar,Form,FormControl,Button } from 'react-bootstrap';



function Navbr() {
  return(
     <div >
         <Navbar expand="lg" className="navbar">
            <div class="left-nav">

                <div class="logo"></div>
                <div class="text">Hospital</div>
             </div>   
                 <div class="menu"> <Navbar.Toggle aria-controls="basic-navbar-nav" class="icon" /></div>
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="mr-auto">
              
              </Nav>
              <Form inline className="navform" >
                  <ul>
                     <li><a href="">Home</a></li>
                     <li><a href="">About Us</a></li>
                     <li><a href="">Doctors</a></li>
                     <li><a href="">Contact Us</a></li>
                  </ul>
              </Form>
            </Navbar.Collapse>
          </Navbar>
     </div>
    
    );

}

export default Navbr;
