import React ,{ useEffect }from 'react';
import './header.css';
import Aos from "aos";
import "aos/dist/aos.css";
import ReactDOM from 'react-dom';

function Header(){
  
  useEffect(() => {
      Aos.init({
      }
      );

  } ,[]);	
   return(
      <div className="header1">
          <div class="header" data-aos="fade-up"
          data-aos-easing="ease-in-sine"
                              data-aos-duration='1500'
                              data-aos-once="false"
                         
                              data-aos-offset="400">

              <div class="clip1"></div>
              <div class="clip2"></div>
              <div class="clip3"></div>
              <div class="clip4"></div>
              <div class="clip5"></div>
               <div class="clip7"></div>
              <div class="clip8"></div>
              <div class="clip9"></div>
              <div class="content">
                <div class="in" >
	              <div class="headertext" data-aos="fade-in" 
                data-aos-easing="ease-in-sine"
	                                      data-aos-duration='1500'
	                                      data-aos-once="false">
	                The best <span>doctor</span> gives the least medicines</div>
	              <div class="button"  data-aos="fade-in"
	                data-aos-duration='1500' data-aos-easing="ease-in-sine">
                      <button class="btn">Appointment
                      </button>


	                </div>
	             </div> 
              </div>
              <div class="image1"  data-aos="fade-in"   data-aos-duration='2000'></div>
           <div class="image2"  data-aos="fade-in"   data-aos-duration='2000'></div>
           
          </div>
      </div>

   	);


}
export default Header;