import React ,{ useEffect ,Component}from 'react';
import './Section3.css';
import Aos from "aos";
import "aos/dist/aos.css";
import ReactDOM from "react-dom";
import AnimatedNumber from "react-animated-number";

export default class Section3 extends React.Component{

	 state = {
    value: 150,
    duration: 1000

  };


  formatValue = value => value.toFixed(0);



    render(){
           
           return(
                  <div class="section3" data-aos="fade-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    offset="30">

                           <div class='insec3' >

                                 <div class="icons">   

                                       
                               
                                        
                                        <div class="mid4" data-aos="flip-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    offset="30">
                                    

                                           
                                                <i class="fas fa-chart-line"></i>

                                           <div className="num2">

                                               <AnimatedNumber className="am"
                                                value={400}
                                                 speed={1000}
                                                formatValue={(value) => (value.toFixed(0))}
                                                duration={10000}
                                              />
                                           </div>


                                           <div class="lf">

                                              Business Partners
                                           </div>


                                       </div>



                               
                                        
                                        <div class="mid4" data-aos="flip-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    data-aos-delay="200"
    offset="30">
                                    

                                           <i class="far fa-heart"></i>  

                                           <div class="num2">

                                                                <AnimatedNumber className="am"
                    									          value={5000}

                    									          speed={1000}
                                                formatValue={(value) => (value.toFixed(0))}
                                                duration={10000}
                    									        />
                                           </div>


                                           <div class="lf">

                                             Happy Patients
                                           </div>


                                       </div>

                                             


                                        
                                        <div class="mid4" data-aos="flip-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    data-aos-delay="300"
    offset="30" >
                                    

                                           <i class="far fa-book"></i>

                                           <div class="num2">

                                                                <AnimatedNumber className="am"
                                                value={500}

                                               speed={1000}
                                                formatValue={(value) => (value.toFixed(0))}
                                                duration={10000}
                                              />
                                           </div>


                                           <div class="lf">

                                              Number Of Staffs
                                           </div>


                                       </div>




                                                     
                                        
                                        <div class="mid4" data-aos="flip-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    data-aos-delay="400"
    offset="30" >
                                    

                                            <i class="far fa-tooth"></i> 

                                           <div class="num2">

                                                                <AnimatedNumber className="am"
                                                value={450}

                                                speed={1000}
                                                formatValue={(value) => (value.toFixed(0))}
                                                duration={10000}
                                              />
                                           </div>


                                           <div class="lf">

                                              Doctors
                                           </div>


                                       </div>



									


                                           


                                               
                                   



                           </div>


</div>



                  </div>                 

            );



    }



}