
import React from 'react';
import './Section6.css';
import Aos from "aos";
import "aos/dist/aos.css"

function Section6() {
 

  return (
    <div>
        
                <div class="section6">





                     <div class="insec6" data-aos="fade-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    data-aos-delay="100"
    offset="10">




                              <div class="sec6left" data-aos="zoom-in-up"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    data-aos-delay="200"
    offset="10">


                                   <div class="head1">We are here For You</div>


                                   <div class="head2">Make An Appointment</div>



                                   <form class="sec6form">
                                     <div class="top-input">
                                            <div class="name">
                                                     <label>Name</label>
                                                     <input placeholder="Your Full Name"/>
                                                 </div>
                                                
                                                <div class="email">
                                                     <label>Email</label>

                                                     <input placeholder="Email"/>
                                                 </div>

                                             </div>







                                      <div class="mid-input">
                                          <div class="number">
                                        

                                           <label>Number</label>
                                             <input placeholder="Phone Number"/>
                                         </div>

                                          <div class="app">
                                        

                                             <label>Appointment Date</label> 
                                             <input placeholder="dd-mm-yy"/>
                                            
                                          </div>

                                     </div>



                                       <div class="down-input">
                                        <div class="apptime">
                                        

                                           <label>Appointment-Time</label>
                                             <input placeholder="hr:min"/>
                                         </div>

                                          <div class="doctor">
                                        

                                             <label>Select a Doctor</label> 
                                             <select>
                                                 <option value="null"></option>
                                                  <option value="">Dr. XYX</option>
                                                   <option value="">Dr. XYX</option>
                                                    <option value="">Dr. XYX</option>


                                             </select>
                                            
                                          </div>

                                     </div>



                                       <div class="last-input">  <div class="number">
                                        

                                           <label>Message</label>
                                             <textarea  placeholder="Message"/>
                                         </div>

                                         

                                       </div>





                                  </form>

                                         <div class="mapbutton">

                                            <button>Submit</button>


                                       </div>


                              </div>


            



                              <div class="right" data-aos="zoom-in-down"
    data-aos-easing="ease-in-sine"
    data-aos-duration="1500"
    data-aos-delay="400"
    offset="10">



                                     <div class="rightheading1">Learn Anything</div>
                                     <div class="rightheading2">We Offer Best Dental Services</div>
                                     <div class="three-cards">
                                            


                                              <div class="first-card">
                                                   <div class="first-card-logo"></div>
                                                   <div class="first-card-text">
                                                         <h1>Easy Booking</h1>
                                                         <h2>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</h2>


                                                   </div>




                                              </div>

                                               <div class="second-card">
                                                   <div class="second-card-logo"></div>
                                                   <div class="second-card-text">
                                                         <h1>Team Dentist</h1>
                                                         <h2>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</h2>


                                                   </div>




                                              </div>



                                                  <div class="third-card">
                                                   <div class="third-card-logo"></div>
                                                   <div class="third-card-text">
                                                         <h1>Best price Guarantee</h1>
                                                         <h2>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts.</h2>


                                                   </div>




                                              </div>







                                     </div>









                              </div>



                     </div>



                </div>


      </div>
  );
}
export default Section6;